import MapView from './MapView'
import { useState, useEffect, useMemo } from 'react'

const steps = 20
function pointToFallline(pt) {
  const coords = pt.geometry.coordinates

  return {
    path: new Array(steps).fill(1).map((_, i) => [coords[0], 90 - ((90 - coords[1]) * i) / steps]),
    timestamps: new Array(steps).fill(1).map((_, i) => i * 500 + pt.properties.delay),
    color: pt.properties.color,
    radius: pt.properties.radius
  }
}

const App = () => {
  const [data, setData] = useState([])

  useEffect(() => {
    fetch('./data.geojson')
      .then(response => response.json())
      .then(data => data.features)
      .then(setData)
  }, [])

  const transformedData = useMemo(() => {
    return data
      .map((f, i) => ({
        ...f,
        id: i,
        properties: {
          ...f.properties,
          color: [181 + 64 * Math.random(), 181 + 64 * Math.random(), 181 + 64 * Math.random()],
          delay: Math.random() * 30000,
          radius: 0.1 + Math.random() * 1.75
        }
      }))
      .map(pointToFallline)
  }, [data])

  return (
    <>
      <div id="layout">
        <MapView data={transformedData} />
        <div id="topRight">
          <h1>Earth's observatories and telescopes</h1>
          <p>According to OpenStreetMap</p>
        </div>

        <div id="credits">
          #30DayMapChallenge - 9 - Space
          <br />
          Source: OpenStreetMap (ODbL).
        </div>

        <div id="author">bbecquet.net</div>
      </div>
    </>
  )
}

export default App
