import { useEffect, useLayoutEffect, useState, useRef, useMemo } from 'react'
import DeckGL from '@deck.gl/react'
import Map from 'react-map-gl'
// eslint-disable-next-line import/no-webpack-loader-syntax
import maplibregl from '!maplibre-gl'
import { ScatterplotLayer } from '@deck.gl/layers'
import { HeatmapLayer } from '@deck.gl/aggregation-layers'
import { TripsLayer } from '@deck.gl/geo-layers'
// import { DataFilterExtension } from '@deck.gl/extensions'
import GL from '@luma.gl/constants'

import maplibreglWorker from 'maplibre-gl/dist/maplibre-gl-csp-worker'
maplibregl.workerClass = maplibreglWorker

const INITIAL_VIEW = {
  longitude: 10,
  latitude: 23,
  zoom: 1.3,
  minZoom: 0,
  maxZoom: 20
}

const MapView = ({ data }) => {
  const [initialViewState, setInitialViewState] = useState(null)
  const [tick, setTick] = useState(0)
  const [fallLayer, setFalllayer] = useState(null)
  const timer = useRef(null)

  useLayoutEffect(() => {
    if (!initialViewState) {
      setInitialViewState(INITIAL_VIEW)
    }
  }, [initialViewState])

  useEffect(() => {
    if (data.length) {
      timer.current = setInterval(() => {
        setTick(prev => prev + 200)
      }, 50)
    }
  }, [data])

  const finishedStars = useMemo(
    () => data.filter(({ timestamps }) => tick > timestamps[timestamps.length - 1]),
    [data, tick]
  )

  useEffect(() => {
    setFalllayer(
      new TripsLayer({
        id: 'trails',
        data,
        currentTime: tick,
        getTimestamps: d => d.timestamps,
        trailLength: 2500,
        getColor: d => d.color,
        getPath: d => d.path,
        getWidth: d => d.radius,
        jointRounded: true,
        capRounded: true,
        opacity: 0.5,
        widthUnits: 'pixels',
        shadowEnabled: false,
        parameters: {
          blend: true,
          blendFunc: [GL.SRC_ALPHA, GL.CONSTANT_ALPHA]
        }
      })
    )
  }, [data, tick])

  const glowLayer = null
  // useMemo(() => {
  //   return new HeatmapLayer({
  //     id: 'heatmap',
  //     data,
  //     opacity: tick < 5000 ? 0 : Math.min(tick - 5000 / 5000, 1),
  //     radiusPixels: 15,
  //     getPosition: d => d.path[d.path.length - 1],
  //     getWeight: 0.5,
  //     threshold: 0.025,
  //     aggregation: 'SUM',
  //     colorRange: [[255, 255, 255]]
  //   })
  // }, [data, tick])

  const starsLayer = useMemo(() => {
    return new ScatterplotLayer({
      id: 'stars',
      data: finishedStars,
      pickable: true,
      opacity: 0.8,
      stroked: false,
      filled: true,
      radiusMinPixels: 1,
      radiusUnits: 'pixels',
      radiusMaxPixels: 100,
      lineWidthMinPixels: 1,
      getPosition: d => d.path[d.path.length - 1],
      getRadius: d => d.radius + (Math.random() * 4 - 2),
      getFillColor: d => d.color.concat([255 * Math.random()]),
      updateTriggers: {
        getRadius: [tick],
        getFillColor: [tick]
      },
      transitions: {
        getRadius: 500,
        getFillColor: 500
      }
    })
  }, [finishedStars, tick])

  return (
    <div id="map">
      {initialViewState && (
        <DeckGL initialViewState={initialViewState} controller={true} layers={[glowLayer, fallLayer, starsLayer]}>
          <Map
            mapLib={maplibregl}
            attributionControl={false}
            mapStyle="https://basemaps.cartocdn.com/gl/dark-matter-nolabels-gl-style/style.json"
          />
        </DeckGL>
      )}
    </div>
  )
}

export default MapView
